@import url(https://fonts.googleapis.com/css2?family=Pacifico&display=swap);
@import url(https://fonts.googleapis.com/css2?family=PT+Sans&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Fredoka+One&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*,
*:before,
*:after {
    box-sizing: border-box;
}
html {
    font-family: 'PT Sans', sans-serif;
    letter-spacing: -0.02rem;
    /* line-height: 1.5; */
    color: rgba(255, 255, 255, 0.8);
    background-color: #4c56a1;
}

a {
    color: inherit;
}

h1 {
    font-family: Pacifico;
    font-weight: 900;
    font-size: 3rem;
    font-size: calc(3rem + 0.25vw + 0.5vh);
    margin: 2rem 0 0;
}

.App {
    display: flex;
    min-height: 100vh;
}

.footer {
    position: fixed;
    bottom: 0;
    width: 50%;
    color: rgba(255, 255, 255, 0.475);
    font-size: 10px;
}

.pageDoc {
    width: 50%;
    height: 50%;
    float: left;
}
.quiz {
    width: 50%;
    height: 50%;
    display: flex;
    right: 0;
    flex-direction: column;
    align-items: center;
    text-align: center;
    padding: 10px;
    position: fixed;
}
.quiz img {
    width: 100px;
    margin-top: 3rem;
}
.quiz p {
    font-size: calc(1rem + 0.25vw + 0.5vh);
    padding: 0 1.5rem;
}

.progress {
    font-family: monospace;
    background: rgba(255, 255, 255, 0.194);
    color: rgba(255, 255, 255, 0.8);
    position: absolute;
    top: 10px;
    right: 10px;
    padding: 0.25rem 1rem;
    border-radius: 3px;
}
button {
    text-transform: uppercase;
    margin: 1.5rem;
    border: 2px solid;
    padding: 1rem 2rem 0.9375rem;
    border-radius: 0.5rem;
    color: rgba(255, 255, 255, 0.8);
    background: transparent;
    letter-spacing: 2px;
    font-weight: 800;
    cursor: pointer;
    transition: 0.1s;
    font-size: calc(1rem + 0.25vw + 0.5vh);
}

button:hover {
    color: #4c56a1;
    background: rgba(255, 255, 255, 0.8);
}

.startBtn {
    -webkit-animation: btnAni 1.5s ease-in-out infinite both;
            animation: btnAni 1.5s ease-in-out infinite both;
}

@-webkit-keyframes btnAni {
    from {
        transform: scale(1);
        transform-origin: center center;
        -webkit-animation-timing-function: liner;
                animation-timing-function: liner;
    }
    50% {
        transform: scale(1.04);
        -webkit-animation-timing-function: liner;
                animation-timing-function: liner;
    }
}

@keyframes btnAni {
    from {
        transform: scale(1);
        transform-origin: center center;
        -webkit-animation-timing-function: liner;
                animation-timing-function: liner;
    }
    50% {
        transform: scale(1.04);
        -webkit-animation-timing-function: liner;
                animation-timing-function: liner;
    }
}
.isIdle p {
    font-family: 'PT Sans', sans-serif;
    font-size: 1.2em;
    font-weight: 400;
}

.score {
    position: fixed;
    background: #4c56a1;
    color: rgba(255, 255, 255, 0.808);
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 100;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: 1em;
    text-align: center;
    line-height: 1.2;
}
.score div {
    background-color: rgba(240, 248, 255, 0.831);
    width: 500px;
    height: 300px;
    text-align: center;
    margin: 50px;
    border-radius: 20px;
    color: #4c56a1;
    line-height: 1.5;
    padding: 20px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    font-size: larger;
}
.score button {
    color: rgba(255, 255, 255, 0.808);
    background: transparent;
}
.score button:hover {
    color: #4c56a1;
    background: rgba(255, 255, 255, 0.808);
    border-color: rgba(255, 255, 255, 0.808);
}
.score span {
    font-size: 5em;
    padding: 10px;
    font-weight: 800;
}

li {
    list-style: none;
}

pre {
    font-family: monospace;
    font-size: calc(1.5em + 0.25vw + 0.5vh);
    color: #4c56a1;
    background-color: aliceblue;
    width: 100%;
    max-width: 600px;
    height: 340px;
    border-radius: 5px;
    padding: 100px 0;

    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.pre-description {
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    font-size: 20px !important;
    width: 80%;
    color: #4c56a1b9;
    margin-top: 20px;
    white-space: pre-line;
}

.pre-string > p {
    display: inline;
    background-color: rgba(190, 204, 215, 0.208);
    padding: 0 3px 0 3px;
}

.pre-bar {
    width: 100%;
    height: 30px;
    font-size: 16px;

    padding: 0 0 0 10px;
    font-weight: 700;
    text-align: left;
    background-color: rgb(190, 204, 215);
    color: #ffffffad;
    border-radius: 5px 5px 0 0;
    position: absolute;
    top: 0;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.pre-displaySelector {
    background-color: rgba(190, 204, 215, 0.208);
    padding: 0 3px 0 3px;
    border-bottom: 3px solid #4c56a1b6;
    height: 50px;
    font-size: 1.1em;
    margin-top: -15px;
    letter-spacing: -3px;
}

.pre-string {
    margin-top: 40px;
    font-size: 0.7em;
    font-weight: 900;
}
pre .pre-fakeButton {
    display: flex;
    text-align: center;
}
pre span:not(.pre-string) {
    color: rgba(74, 74, 74, 0.913);
    font-size: 14px;
    background-color: rgb(147, 161, 173);
    margin-right: 5px;
    display: block;
    width: 20px;
    height: 20px;
    border-radius: 20px;
}
.pre-fakeButton :nth-child(3) {
    background-color: rgb(233, 78, 78);
}
.pre-fakeButton :nth-child(2) {
    background-color: rgb(233, 228, 78);
}
.pre-fakeButton :nth-child(1) {
    background-color: rgb(109, 203, 104);
}

.correct {
    color: #4ac714;
}
.wrong {
    color: #ee3c1c;
}
.checked.correct {
    -webkit-animation: yay 1.5s ease-in-out infinite both;
            animation: yay 1.5s ease-in-out infinite both;
}
@-webkit-keyframes yay {
    from {
        transform: scale(1);
        transform-origin: center center;
        -webkit-animation-timing-function: ease-out;
                animation-timing-function: ease-out;
    }
    50% {
        transform: scale(1.3);
        -webkit-animation-timing-function: ease-out;
                animation-timing-function: ease-out;
    }
}
@keyframes yay {
    from {
        transform: scale(1);
        transform-origin: center center;
        -webkit-animation-timing-function: ease-out;
                animation-timing-function: ease-out;
    }
    50% {
        transform: scale(1.3);
        -webkit-animation-timing-function: ease-out;
                animation-timing-function: ease-out;
    }
}
.checked.wrong {
    -webkit-animation: shake-horizontal 0.8s cubic-bezier(0.455, 0.03, 0.515, 0.955)
        both;
            animation: shake-horizontal 0.8s cubic-bezier(0.455, 0.03, 0.515, 0.955)
        both;
}
@-webkit-keyframes shake-horizontal {
    0%,
    100% {
        transform: translateX(0);
    }
    10%,
    30%,
    50%,
    70% {
        transform: translateX(-10px);
    }
    20%,
    40%,
    60% {
        transform: translateX(10px);
    }
    80% {
        transform: translateX(8px);
    }
    90% {
        transform: translateX(-8px);
    }
}
@keyframes shake-horizontal {
    0%,
    100% {
        transform: translateX(0);
    }
    10%,
    30%,
    50%,
    70% {
        transform: translateX(-10px);
    }
    20%,
    40%,
    60% {
        transform: translateX(10px);
    }
    80% {
        transform: translateX(8px);
    }
    90% {
        transform: translateX(-8px);
    }
}
.label-ctnr {
    display: flex;
    align-items: center;
    justify-content: center;
}
.label-ctnr label {
    position: relative;
    left: -0.75rem;
    padding: 0.5rem 1rem 0.5rem 1.5rem;
    border-radius: 5px;
    font-weight: 700;
    font-family: 'Fredoka One', cursive;
    text-transform: uppercase;
    font-size: calc(1.5rem + 0.25vw + 0.5vh);
    display: flex;
    align-items: center;
    transition: 0.3s;
}
.label-ctnr label span {
    position: absolute;
    left: 0;
}
.label-ctnr:not(.disabled) label:hover {
    cursor: pointer;
}
.label-ctnr:not(.disabled) label:hover {
    transform: scale(1.1);
}
.label-ctnr input {
    opacity: 0;
    position: absolute;
}
.label-ctnr svg {
    width: 1.5rem;
    display: block;
}

.path {
    stroke-dasharray: 1000;
    stroke-dashoffset: 0;
}
.path.line {
    stroke-dashoffset: 1000;
    -webkit-animation: dash 1s 0.15s ease-in-out forwards;
            animation: dash 1s 0.15s ease-in-out forwards;
}
.path.line + .path.line {
    -webkit-animation: dash 0.9s 0.35s ease-in-out forwards;
            animation: dash 0.9s 0.35s ease-in-out forwards;
}
.path.check {
    stroke-dashoffset: -100;
    -webkit-animation: dash-check 0.9s 0.35s ease-in-out forwards;
            animation: dash-check 0.9s 0.35s ease-in-out forwards;
}

@-webkit-keyframes dash {
    0% {
        stroke-dashoffset: 1000;
    }
    100% {
        stroke-dashoffset: 0;
    }
}

@keyframes dash {
    0% {
        stroke-dashoffset: 1000;
    }
    100% {
        stroke-dashoffset: 0;
    }
}

@-webkit-keyframes dash-check {
    0% {
        stroke-dashoffset: -100;
    }
    100% {
        stroke-dashoffset: 900;
    }
}

@keyframes dash-check {
    0% {
        stroke-dashoffset: -100;
    }
    100% {
        stroke-dashoffset: 900;
    }
}

.fade-enter-active,
.fade-leave-active {
    transition: opacity 0.5s;
}
.fade-enter,
.fade-leave-to {
    opacity: 0;
}

.fade2-enter-active,
.fade2-leave-active {
    transition: all 1s 0s;
    transform: scale(1);
}

.fade2-enter,
.fade2-leave-to {
    opacity: 0;
    transform: scale(0.5);
}

.pageDocs {
    box-sizing: border-box;
    padding: 0.5rem 0.25rem;
    min-height: 100vh;
    float: left;
    width: 100%;
    height: 100%;
    background-color: aliceblue;
    color: #5273a5;
}

.pageDocs :nth-child(n) {
    font-family: monospace;
}
.pageDocs *,
.pageDocs *:before,
.pageDocs *:after {
    font-size: 0.875rem;
    font-weight: normal;
    text-decoration: none;
    cursor: default;
}
.pageDocs input,
.pageDocs span {
    display: block;
}

[data-content1]:before {
    content: attr(data-content1);
}
[data-content2]:after {
    content: attr(data-content2);
}
[data-content]:before {
    content: attr(data-content);
}
.pageDocs * {
    border: 3px solid transparent;
}
.pageDocs :nth-child(n) {
    margin: 0.6rem;
    padding: 0.2rem;
    line-height: 1.2;
}

.pageDocs :nth-child(n) > :nth-child(n) {
    margin-left: 2rem;
}

.pageDocs * {
    transition: border 0.3s;
}
.pageDocs *:hover {
    box-shadow: 0 0 0 1px;
    border-radius: 5px;
}

.pageDocs input {
    display: block;
    border: 1px inset;
}

